import './style.css'
import * as THREE from 'three'
import { gsap } from "gsap";
import { CSSRulePlugin } from "gsap/dist/CSSRulePlugin";
gsap.registerPlugin(CSSRulePlugin);
import { OrbitControls } from 'three/examples/jsm/controls/OrbitControls.js'
import * as dat from 'dat.gui'

// Loading
const textureLoader = new THREE.TextureLoader()
const normalTexture = textureLoader.load('https://raw.githubusercontent.com/ziasyed2000/ziasyed2000.github.io/main/NormalMap.png')

// Debug
// const gui = new dat.GUI()

// Canvas
const canvas = document.querySelector('canvas.webgl')

// Scene
const scene = new THREE.Scene()

// Objects
const geometry = new THREE.SphereBufferGeometry(.5, 64, 64)

// Materials
const material = new THREE.MeshStandardMaterial()
material.metalness = 0.7
material.roughness = 0.2
material.normalMap = normalTexture
// material.bumpScale = 1
material.color = new THREE.Color(0x292929)

// Mesh
const sphere = new THREE.Mesh(geometry,material)
scene.add(sphere)

// Light 1
const pointLight = new THREE.PointLight(0xffffff, 0.1)
pointLight.position.x = 2
pointLight.position.y = 3
pointLight.position.z = 4
scene.add(pointLight)

// Light 2
const pointLight2 = new THREE.PointLight(0xff0000, 2)
pointLight2.position.set(-1.86,1,-1.65)
pointLight2.intensity = 10
scene.add(pointLight2)

// Light 3
const pointLight3 = new THREE.PointLight(0xe1ff, 2)
pointLight3.position.set(2.13,-3,-1.98)
pointLight3.intensity = 10
scene.add(pointLight3)

/**
 * Sizes
 */
const sizes = {
    width: window.innerWidth,
    height: window.innerHeight
}

window.addEventListener('resize', () =>
{
    // Update sizes
    sizes.width = window.innerWidth
    sizes.height = window.innerHeight

    // Update camera
    camera.aspect = sizes.width / sizes.height
    camera.updateProjectionMatrix()

    // Update renderer
    renderer.setSize(sizes.width, sizes.height)
    renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))
})

/**
 * Camera
 */
// Base camera
const camera = new THREE.PerspectiveCamera(75, sizes.width / sizes.height, 0.1, 100)
camera.position.x = 0
camera.position.y = 0
camera.position.z = 2
scene.add(camera)

/**
 * Renderer
 */
const renderer = new THREE.WebGLRenderer({
    canvas: canvas
})
renderer.setSize(sizes.width, sizes.height)
renderer.setPixelRatio(Math.min(window.devicePixelRatio, 2))

/**
 * Animate
 */
document.addEventListener('mousemove', onDocumentMouseMove)

let mouseX = 0;
let mouseY = 0;
 
let targetX = 0;
let targetY = 0;
 
const windowX = window.innerWidth / 2;
const windowY = window.innerHeight / 2;
 
function onDocumentMouseMove(event) {
    mouseX = (event.clientX - windowX)
    mouseY = (event.clientY - windowY)
}
 
const updateSphere = (event) => {
    sphere.position.y = window.scrollY * 0.001
}
 
window.addEventListener('scroll', updateSphere)

const clock = new THREE.Clock()

const tick = () =>
{
    targetX = mouseX * .001
    targetY = mouseY * .001

    const elapsedTime = clock.getElapsedTime()

    // Update objects
    sphere.rotation.y = .5 * elapsedTime

    sphere.rotation.y += .5 * (targetX - sphere.rotation.y)
    sphere.rotation.x += .05 * (targetY - sphere.rotation.x)
    sphere.position.z += -.05 * (targetY - sphere.rotation.x)

    // Render
    renderer.render(scene, camera)

    // Call tick again on the next frame
    window.requestAnimationFrame(tick)
}

tick()

/////////////////////////////////////////////////////////////////////////////////////////////////////////////////

const h1 = document.querySelector('h1')
const b1 = document.querySelector('.explore-button')
const b2 = document.querySelector('.resume-button')
const modal = document.querySelector('.modal')

const body = document.getElementsByTagName("body");

const tl = gsap.timeline()

const fadeOut = function () {
    tl.to(h1, {duration: 1, opacity: 0, y: '-50px'})
    tl.to(b1, {duration: 1, opacity: 0, y: '-75px'}, '-=1')
    tl.to(b2, {duration: 1, opacity: 0, y: '-75px'}, '-=1')




    let counter = 1;
    let size = -.05;
    
    setInterval(() => {
        if (counter < 10) {
        counter++;
        size = size + .01
        }
    }, 100);
    

    const clock = new THREE.Clock()

    const tick = () =>
    {
        targetX = mouseX * .001
        targetY = mouseY * .001

        const elapsedTime = clock.getElapsedTime()

        // Update objects
        sphere.rotation.y = counter * elapsedTime

        sphere.rotation.y += .5 * (targetX - sphere.rotation.y)
        sphere.rotation.x += .05 * (targetY - sphere.rotation.x)
        sphere.position.z += size

        // Render
        renderer.render(scene, camera)

        // Call tick again on the next frame
        window.requestAnimationFrame(tick)
    }
    tick()  
}

b1.addEventListener('click', function (e) {
    e.preventDefault();

    console.log("pressed");

    fadeOut();

    setTimeout(() => {
        // tl.to(modal, {opacity: 100, height: '90vh', width: '90%'})
        window.location = "https://ziasyed2000.github.io/Portfolio/index.html";

        tl.to(h1, {display: 'none'})
        tl.to(b1, {display: 'none'})
        tl.to(b2, {display: 'none'})
    }, 1750);
})